.ArtLink {
  position: fixed;
  top: 30px;
  right: 30px;
  font-size: 10px;
  cursor: pointer;
  text-decoration: none;
  color: #000;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}
.ArtLink span {
  -webkit-writing-mode: vertical-rl;
      -ms-writing-mode: tb-rl;
          writing-mode: vertical-rl;
  padding-top: 4px;
  padding-left: 1px;
  display: block;
}
.ArtLink:hover span {
  color: #fff;
}
.ArtLink.ArtLinkDark:hover span {
  color: #808080;
}

.LogoFill {
  fill: #000;
}
.ArtLink.ArtLinkDark:hover .LogoFill {
  fill: rgb(128, 128, 128);
}
.ArtLink:hover .LogoFill {
  fill: #fff;
}



