.Title {
  position: absolute;
  top: 30px;
  left: 30px;
}
h1 {
  font-size: 13.75vh;
  font-weight: 900;
  font-family: var(--alergia-black);
  line-height: 0.88;
  margin-bottom: 30px;
  cursor: default;
  -webkit-transform: translateY(-19px);
      -ms-transform: translateY(-19px);
          transform: translateY(-19px);
}
.Subtitle {
  cursor: default;
  -webkit-transform: translateY(-19px);
      -ms-transform: translateY(-19px);
          transform: translateY(-19px);
}



